import React from "react";
import PropTypes from "prop-types";
import { graphql, Link } from "gatsby";
import Helmet from "react-helmet";
import { readingTime as readingTimeHelper } from "@tryghost/helpers";
import Moment from "react-moment";

import { Layout } from "../components/common";
import { MetaData } from "../components/common/meta";
import "../components/common/Layout/post.scss";

/**
 * Single post view (/:slug)
 *
 * This file renders a single post and loads all the content.
 *
 */
const Post = ({ data, location }) => {
    const post = data.ghostPost;
    const readingTime = readingTimeHelper(post);
    const tags = data.tags.edges;
    const allGhostPost = data.allGhostPost.edges;
    const otherPosts = data.otherPost.edges;
    console.log(`otherPosts`, otherPosts);
    return (
        <div className="first-section">
            <MetaData data={data} location={location} type="article" />
            <Helmet>
                <style type="text/css">{`${post.codeinjection_styles}`}</style>
            </Helmet>
            <Layout location="/blog/">
                <div className="container pt-5 text-center">
                    {tags.map(({ node }) => (
                        <Link
                            key={node.slug}
                            to={`tag/${node.slug}`}
                            className="text-white mr-2"
                        >
                            <span className="badge text-green rounded-pill py-2 px-3 mt-2 mt-md-0">
                                {node.name}
                            </span>
                        </Link>
                    ))}
                    <Link
                        key="all"
                        to="articles/all"
                        className="text-white mr-2 mb-3 mb-md-0"
                    >
                        <span className="badge text-green rounded-pill py-2 px-3 mt-2 mt-md-0">
                            All
                        </span>
                    </Link>
                </div>
                <div className="container">
                    <div className="row">
                        <article className="post-details">
                            <header className="post-full-header">
                                <section className="post-full-tags">
                                    <a
                                        href={`/tag/${post.primary_tag.slug}`}
                                        className="text-green"
                                    >
                                        {post.primary_tag.name}
                                    </a>
                                </section>

                                <h1 className="post-full-title font-weight-bold">
                                    {post.title}
                                </h1>

                                <p className="post-full-custom-excerpt">
                                    {post.excerpt}
                                </p>

                                <div className="post-full-byline">
                                    <section className="post-full-byline-content">
                                        <ul className="author-list">
                                            <li className="author-list-item">
                                                <a
                                                    href="/author/gojek/"
                                                    className="author-avatar"
                                                >
                                                    {post.primary_author
                                                        .profile_image ? (
                                                        <img
                                                            className="author-profile-image"
                                                            src={
                                                                post
                                                                    .primary_author
                                                                    .profile_image
                                                            }
                                                            alt={
                                                                post
                                                                    .primary_author
                                                                    .name
                                                            }
                                                        />
                                                    ) : (
                                                        <img
                                                            className="default-avatar"
                                                            src="/images/icons/avatar.svg"
                                                            alt={
                                                                post
                                                                    .primary_author
                                                                    .name
                                                            }
                                                        />
                                                    )}
                                                </a>
                                            </li>
                                        </ul>

                                        <section className="post-full-byline-meta">
                                            <h4 className="author-name">
                                                <a
                                                    href={`tag/${post.primary_author.slug}`}
                                                >
                                                    {post.primary_author.name}
                                                </a>
                                            </h4>
                                            <div className="byline-meta-content">
                                                <time
                                                    className="byline-meta-date"
                                                    dateTime="2020-05-22"
                                                >
                                                    <Moment format="MMM DD, YYYY">
                                                        {post.published_at}
                                                    </Moment>
                                                </time>
                                                <span className="byline-reading-time">
                                                    <span className="bull">
                                                        &bull;
                                                    </span>
                                                    {` `}
                                                    <span>{readingTime}</span>
                                                    {` `}
                                                    &nbsp;| &nbsp;
                                                </span>
                                            </div>
                                        </section>
                                    </section>
                                </div>
                            </header>
                            {/* Post image */}
                            <figure className="post-full-image">
                                <img
                                    src={post.feature_image}
                                    alt={post.title}
                                />
                            </figure>
                            {/* End Post image */}

                            {/* Content */}
                            <section className="post-full-content">
                                <div className="post-content">
                                    <div
                                        dangerouslySetInnerHTML={{
                                            __html: post.html,
                                        }}
                                    />
                                </div>
                            </section>
                            {/* End Content */}
                        </article>
                    </div>
                </div>
                {/* Read More */}
                <aside className="read-next outer py-5">
                    <div className="container">
                        <div className="row">
                            <div className="col-md-3">
                                <article className="read-next-card">
                                    <header className="read-next-card-header">
                                        <h3 className="mb-4">
                                            <span>Recent posts</span>
                                        </h3>
                                    </header>
                                    <div className="read-next-card-content">
                                        <ul>
                                            {allGhostPost.map(({ node }) => (
                                                <li>
                                                    <h4>
                                                        <a
                                                            href={`/blog/${node.slug}`}
                                                        >
                                                            {node.title}
                                                        </a>
                                                    </h4>
                                                    <div className="read-next-card-meta">
                                                        <p>
                                                            <Moment format="MMM DD, YYYY">
                                                                {
                                                                    node.published_at
                                                                }
                                                            </Moment>
                                                            {` `}–{` `}
                                                            {readingTimeHelper(
                                                                node
                                                            )}
                                                        </p>
                                                    </div>
                                                </li>
                                            ))}
                                        </ul>
                                    </div>
                                    <footer className="read-next-card-footer">
                                        <a href="/tag/tech/">See all posts →</a>
                                    </footer>
                                </article>
                            </div>
                            {otherPosts.map(({ node }) => (
                                <div className="col-md-6 col-lg-4">
                                    <Link
                                        to={`/blog/${node.slug}`}
                                        className="post-card"
                                    >
                                        {node.feature_image && (
                                            <div
                                                className="post-card-image"
                                                style={{
                                                    backgroundImage: `url(${node.feature_image})`,
                                                }}
                                            ></div>
                                        )}
                                        <h3 className="text-white maison-bold font-xs title pt-3">
                                            {node.title}
                                        </h3>
                                        <small className="excerpt text-white ">
                                            {`${node.excerpt.substring(
                                                0,
                                                100
                                            )}...`}
                                        </small>
                                        <footer className="footer font-xs mt-3">
                                            <div className="author text-white font-weight-bold">
                                                <small>
                                                    {node.primary_author.name}
                                                </small>
                                            </div>
                                            <div className="date-time text-white">
                                                <small>
                                                    {readingTimeHelper(node)}
                                                </small>{" "}
                                                &nbsp;| &nbsp;
                                                <small>
                                                    <Moment format="MMM DD, YYYY">
                                                        {node.published_at}
                                                    </Moment>
                                                </small>
                                            </div>
                                        </footer>
                                    </Link>
                                </div>
                            ))}
                        </div>
                    </div>
                </aside>
                {/* End Read More */}
            </Layout>
        </div>
    );
};

Post.propTypes = {
    data: PropTypes.shape({
        ghostPost: PropTypes.shape({
            codeinjection_styles: PropTypes.object,
            title: PropTypes.string.isRequired,
            html: PropTypes.string.isRequired,
            feature_image: PropTypes.string,
        }).isRequired,
    }).isRequired,
    location: PropTypes.object.isRequired,
};

export default Post;

export const postQuery = graphql`
    query($slug: String!) {
        ghostPost: ghostPost(slug: { eq: $slug }) {
            ...GhostPostFields
        }
        tags: allGhostTag {
            edges {
                node {
                    name
                    slug
                }
            }
        }
        allGhostPost: allGhostPost(
            limit: 4
            sort: { order: DESC, fields: [published_at] }
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }
        otherPost: allGhostPost(limit: 2) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }
    }
`;
